/* СЕЛЕКТ -----------------------------
---------------------------------------*/
/* Основная часть */
.cusel {
  position: relative;
  z-index: 1;
  display: inline-block;
  height: 25px;
  font-size: 12px;
  border: 1px solid #EAEAEA;
  background: #fff;
  vertical-align: middle;
  cursor: pointer; }

/* Стрелочка справа */
.cuselFrameRight {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  height: 100%;
  width: 20px;
  background: url(../images/select-arrow.png) no-repeat center; }

/* Текст в селекте */
.cuselText {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 25px;
  padding: 0 25px 0 5px;
  font-size: 12px;
  line-height: 25px;
  color: #7e7e7f;
  cursor: pointer;
  overflow: hidden; }

* html .cuselText {
  height: 30px; }

/* Оформление пунктов списка */
.cusel span {
  display: block;
  padding: 5px 15px;
  font-weight: bold;
  font-size: 13px;
  zoom: 1;
  cursor: pointer;
  white-space: nowrap; }

/* Оформление пунктов списка при наведении*/
.cusel span:hover,
.cusel .cuselOptHover {
  background: #eee; }

/* Оформление активного пункта списка  */
.cusel .cuselActive,
.cusel .cuselActive:hover {
  background: #DA0612;
  color: #fff; }

/* Оформление селекта при наведении и клике */
.cusel:hover,
.cusel:focus,
.cuselFocus {
  background: transparent; }

/* Оформление стрелки при наведении и клике */
.cusel:hover .cuselFrameRight,
.cusel:focus .cuselFrameRight,
.cuselFocus .cuselFrameRight {
  background: transparent url(../images/select-arrow.png) no-repeat center; }

/* Видимость выпадающего списка */
.cuselOpen {
  z-index: 999; }

/* Оформление селекта и стрелки disabled */
.classDisCusel,
.classDisCusel .cuselFrameRight {
  background-image: url(../images/select-arrow.png) no-repeat center;
  cursor: default;
  color: #ccc; }

/* Оформление текста в селекте когда он disabled */
.classDisCusel .cuselText {
  cursor: default;
  color: #ccc; }

/* Оформление выпадающего списка */
.cusel .cusel-scroll-wrap {
  position: absolute;
  left: 0;
  top: 100%;
  display: block;
  min-width: 100%;
  width: auto;
  height: auto !important;
  visibility: hidden;
  margin-top: 5px;
  padding: 5px 0;
  border: 1px solid rgba(0, 0, 0, 0.15);
  background: #fff;
  overflow: hidden;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -o-border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  -moz-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  -ms-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  -o-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175); }

.cusel .jScrollPaneContainer {
  position: relative;
  z-index: 5;
  overflow: hidden;
  background: #fff; }

/* Блок для элементов списка с отступом справа для скролла */
.cusel-scroll-pane {
  top: 5px;
  right: 3px;
  left: 0;
  width: auto !important; }

/* Линия по которой будет проходить скролл */
.cusel .jScrollPaneTrack {
  position: absolute;
  top: 0;
  right: 2px;
  height: 100%;
  width: 5px !important;
  background: #EEE; }

/* Кнопка для скролла */
.cusel .jScrollPaneDrag {
  position: absolute;
  left: 0px;
  width: 5px !important;
  height: 27px !important;
  background: #6E939E;
  cursor: pointer;
  overflow: hidden;
  border-radius: 3px; }

/* Эти 2 стиля не трогаем */
.cusel .jScrollPaneDragTop {
  position: absolute;
  top: 0;
  left: 0;
  width: 0 !important;
  overflow: hidden; }

.cusel .jScrollPaneDragBottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0 !important;
  overflow: hidden; }

/* Стрелка вверху для скролла */
.cusel .jScrollArrowUp {
  position: absolute;
  top: 0;
  right: 4px;
  width: 0 !important;
  height: 0;
  cursor: pointer;
  overflow: hidden; }

/* Стрелка внизу для скролла */
.cusel .jScrollArrowDown {
  width: 0 !important;
  height: 0;
  position: absolute;
  top: auto;
  bottom: 0;
  right: 4px;
  cursor: pointer;
  overflow: hidden; }

/* ПОЛЗУНОК -----------------------------
---------------------------------------*/
.ui-slider {
  margin-bottom: 10px;
  position: relative;
  text-align: left; }

/* Ширина, высота и позиционирование для ползунков
----------------------------------*/
.ui-slider .ui-slider-handle {
  position: absolute;
  z-index: 2;
  width: 15px;
  height: 15px;
  cursor: default;
  -ms-touch-action: none;
  touch-action: none;
  cursor: pointer; }

.ui-slider .ui-slider-range {
  position: absolute;
  z-index: 1;
  font-size: .7em;
  display: block;
  border: 0;
  background-position: 0 0; }

/* support: IE8 - See #6727 */
.ui-slider.ui-state-disabled .ui-slider-handle,
.ui-slider.ui-state-disabled .ui-slider-range {
  filter: inherit; }

/* Высота горизонтальной полоски
----------------------------------*/
.ui-slider-horizontal {
  height: 6px; }

.ui-slider-horizontal .ui-slider-handle {
  top: -4px;
  margin-left: -8px; }

.ui-slider-horizontal .ui-slider-range {
  top: 0;
  height: 100%; }

.ui-slider-horizontal .ui-slider-range-min {
  left: 0; }

.ui-slider-horizontal .ui-slider-range-max {
  right: 0; }

/* Ширина вертикальной полоски
----------------------------------*/
.ui-slider-vertical {
  width: .8em;
  height: 100px; }

.ui-slider-vertical .ui-slider-handle {
  left: -.3em;
  margin-left: 0;
  margin-bottom: -.6em; }

.ui-slider-vertical .ui-slider-range {
  left: 0;
  width: 100%; }

.ui-slider-vertical .ui-slider-range-min {
  bottom: 0; }

.ui-slider-vertical .ui-slider-range-max {
  top: 0; }

